
































import { Component, Vue, Provide } from "vue-property-decorator";
import { State, Getter, Mutation, Action } from "vuex-class";
@Component({
  components: {}
})
export default class UserInfo extends Vue {
  @Getter("user") getUser: any

  @Provide() userData: { username: String; pwd: String } = {
    username: "",
    pwd: ""
  }

  @Provide() loading: boolean = false //是否发起网络请求

  get userImg(): String {
    return require(`@/assets/${this.getUser.key}.jpg`)
  }
  onSubmit() {
    this.userData.username = this.getUser.username
    this.loading = true;

    //发起请求
    (this as any).$axios
      .post("/api/users/changePwd", this.userData)
      .then((result: any) => {
        this.loading = false
        this.$message({
          message: result.data.msg,
          type: "success"
        })
      }).catch((err: any) => {
        this.loading = false
      });

  }
}
